/*
    Универсальный миксин
    для подключения любого шрифта и начертания
*/
@mixin fonts($family, $style: normal, $weight: 400, $dir, $type) {
  @font-face {
    font-family: $family;
    font-style: $style;
    font-weight: $weight;
    font-display: swap;
    src:
      url($dir + $family + $type) format('truetype'),
      url($dir + $family + $type) format('woff'),
      url($dir + $family + $type) format('woff2');
  }
}
/*  
    Набор "Типографика" из Фигмы
    Похоже, что на данный момент будем использовать
    именно этот набор
*/
@mixin Header {
  font-family: Raleway-Bold, sans-serif;
  font-size: 28px;
  line-height: 140%;
}
@mixin Body {
  font-family: Raleway-Medium, sans-serif;
  font-size: 16px;
  line-height: 145%;
}
@mixin Link {
  font-family: Raleway-Medium, sans-serif;
  font-size: 16px;
  line-height: 155%;
}
@mixin Code {
  font-family: Roboto, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 140%;
  font-style: normal;
}
@mixin Numbers {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 140%;
  font-style: normal;
}

@mixin noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* IE / Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
}
