.validation-tooltip{display:flex;flex-direction:column;box-sizing:border-box;max-width:370px;height:min-content;margin-left:14px;padding:20px 24px;border-radius:8px;background:#ffd1d1;font-family:Raleway-Medium,sans-serif;font-size:16px;line-height:145%;text-align:left;transition:opacity .1s ease-in;z-index:20}.validation-tooltip:before{position:absolute;top:15px;left:-10px;content:"";width:20px;height:20px;transform:rotate(45deg);background:#ffd1d1}.validation-tooltip__text{margin:0}.validation-tooltip__list{margin:0;padding-left:10px}.validation-tooltip__item{padding-left:10px}.validation-tooltip__item::marker{content:"•"}